import {useEffect, useRef, useState} from "react";
import { userStore } from "../store/uiStore"; //    

const useAccurateEnergyRestoration = (restoreRate: number) => {
    const lastUpdateTimeRef = useRef(Date.now());
    // const { energy, maxEnergy, setEnergy } = userStore(); //      

    const energy = userStore((state) => state.energy);
    const maxEnergy = userStore((state) => state.maxEnergy);
    const setEnergy = userStore((state) => state.setEnergy);


    useEffect(() => {
        const intervalId = setInterval(() => {
            const now = Date.now();
            const timeDiff = (now - lastUpdateTimeRef.current) / 1000; //   

            // console.log(`: ${now},  : ${lastUpdateTimeRef.current},  : ${timeDiff.toFixed(4)} `);

            //       
            const energyToRestore = timeDiff * restoreRate;
            const newEnergy = Math.min(energy + energyToRestore, maxEnergy); //  ,  maxEnergy

            // console.log(`   ${timeDiff.toFixed(4)} : ${energyToRestore.toFixed(4)}.  : ${newEnergy.toFixed(4)}`);

            if (newEnergy !== energy) {
                setEnergy(newEnergy); //    
                // console.log(` : ${newEnergy.toFixed(4)}  ${maxEnergy}`);
            } else {
                // console.log('  ,     .');
            }

            lastUpdateTimeRef.current = now; //    
        }, 1000); //    1 

        return () => clearInterval(intervalId); //     
    }, [energy, restoreRate, maxEnergy, setEnergy]);
};

export default useAccurateEnergyRestoration;
