import { useEffect, useRef } from 'react';
import { syncClicks } from '../api/api'; //    API 

export const useClickSync = () => {
    const clicksRef = useRef(0); //  useState  useRef   
    const timerRef = useRef<NodeJS.Timeout | null>(null);

    //    
    const setClicksToSync = async (totalClicks: number) => {
        try {
            console.log(`: ${totalClicks} `);
            await syncClicks(totalClicks); //   
            clicksRef.current = 0; //  ref  
        } catch (error) {
            console.error('  :', error);
        }
    };

    const handleClick = () => {
        clicksRef.current += 1; //    ref  

        //  
        if (timerRef.current) clearTimeout(timerRef.current);

        //     3 
        timerRef.current = setTimeout(() => {
            setClicksToSync(clicksRef.current); //     ref
        }, 3000);
    };

    //     
    useEffect(() => {
        return () => {
            if (timerRef.current) clearTimeout(timerRef.current);
        };
    }, []);

    return handleClick;
};
