import { create } from 'zustand';

//    
type ResponseStatus = 'have_answer' | 'in_progress' | 'complete' | 'received_reward' | null;

export interface Question  {
    id: number,
    time_sent:string,
    u_text: string,
    answer: string,
    status: ResponseStatus,
    user_id: number
    secret: boolean
}

//  
interface ChatState {
    //states
    questionsHistory: Question[]; //    
    lastQuestionDate: Date | null; //   
    userQuestion: string; //  
    aiAnswer: string; //  
    questionStatus: ResponseStatus; //   

    //actions
    addMessage: (question: Question) => void; //    
    setUserQuestion: (question: string) => void; //     
    setLastQuestionDate: (date: Date) => void; //      
    setQuestionStatus: (status: ResponseStatus) => void; //     
    setAiAnswer: (answer: string) => void; //     
    setQuestionsHistory: (questionsHistory: Question[]) => void;
}

//  zustand store
export const useChatStore = create<ChatState>((set) => ({
    questionsHistory: [],
    lastQuestionDate: null,
    userQuestion: '',
    aiAnswer: '',
    questionStatus: null, //    complete
    addMessage: (question) => set((state) => ({ questionsHistory: [...state.questionsHistory, question] })),
    setUserQuestion: (question) => set({ userQuestion: question }),
    setLastQuestionDate: (date) => set({ lastQuestionDate: date }),
    setQuestionStatus: (status) => set({ questionStatus: status }), //   
    setAiAnswer: (answer) => set({ aiAnswer: answer }), //   
    setQuestionsHistory: (questionsHistory) => set({ questionsHistory}), //   
}));
