// hooks/useRewardPopup.ts
import { useState, useEffect } from 'react';
import { claimRewards, getQuestionStatus } from '../api/api';
import { useRewardStore } from '../store/rewardStore';
import {updateUiWithTelegramData} from "../services/uiSyncService"; // ,    Zustand      

export const useRewardPopup = () => {
    const [isRewardPopupVisible, setIsRewardPopupVisible] = useState(false);
    const [popupRewards, setPopupRewards] = useState<any[]>([]); //   
    const rewards = useRewardStore((state) => state.rewards); //    

    useEffect(() => {
        if (rewards.length > 0) {
            setPopupRewards(rewards); //      
            setIsRewardPopupVisible(true); //  
        }
    }, [rewards]); //     

    const handleClosePopup = async () => {
        await claimRewards(); //  
        await getQuestionStatus(); //   
        setIsRewardPopupVisible(false); //  
        updateUiWithTelegramData()
    };

    return {
        isRewardPopupVisible,
        popupRewards,
        handleClosePopup
    };
};
