import {create} from 'zustand';
import { claimRewards, getRewardsList} from "../api/api";

export interface Reward {
    id: number;
    type: string;
    amount: number;
    inspirations: number;
    replenishments: number;
}

interface RewardResponseData {
    rewards: Reward[];
}

interface RewardResponseMessage {
    status: string;
    text: string;
}

// export interface RewardResponse {
//     data: RewardResponseData;
//     message: RewardResponseMessage;
// }

interface RewardStoreState {
    rewards: Reward[];
    claimRewards: () => void
    setRewards: (rewards: Reward[]) => void;
}


//  Zustand store
export const useRewardStore = create<RewardStoreState>((set, get) => ({
    rewards: [], //     

    //     (  API)

    setRewards: (rewards) => set({ rewards: rewards }),

    claimRewards: async () => {
        await claimRewards()
        set({rewards: []});
    }
}));


