import {create} from 'zustand';

//   
interface MiningState {
    nextMiningDate: Date;  //   
    timeLeft: number;  //    
    isMining: boolean;  //  
    startMiningStore: (nextMiningDate: Date) => void;  //   
    stopMining: () => void;  //  
    updateTimer: () => void;  //  
    setIsMining: (state: boolean) => void;  //  
}

//         
const calculateTimeLeft = (nextMiningDate: Date, currentDate: Date): number => {
    const diffInMs = nextMiningDate.getTime() - currentDate.getTime();
    return Math.floor(diffInMs / 1000);  //    
};

//  Zustand store  
export const useMiningStore = create<MiningState>((set, get) => ({
    nextMiningDate: new Date(Date.now() + 8 * 60 * 60 * 1000),
    timeLeft: 8 * 60 * 60,
    isMining: false,

    //     
    startMiningStore: (nextMiningDate: Date) => {
        console.log('nextMiningDate',nextMiningDate)
        set({nextMiningDate, isMining: true});

        //       
        const timerInterval = setInterval(() => {
            const {nextMiningDate, updateTimer} = get();

            if (nextMiningDate) {
                const timeLeft = calculateTimeLeft(nextMiningDate, new Date());

                if (timeLeft > 0) {
                    updateTimer();
                } else {
                    clearInterval(timerInterval);  //  ,   
                    set({timeLeft: 0});  //  
                }
            }
        }, 1000);
    },

    //       
    updateTimer: () => {
        set((state) => ({
            timeLeft: calculateTimeLeft(state.nextMiningDate as Date, new Date()),
        }));
    },

    //    
    stopMining: () => {
        set({isMining: false, timeLeft: 0});
    },
    setIsMining: (state) => {
        set({isMining: state});
    },

}));
